<template>
  <div id="app">

    <div class="row no-gutters">
      <div class="col-sm-3">
        <div class="toolbox">
          <div class="sticky-top bg-white shadow-sm p-2">
            <div class="form-group d-flex">
              <label for="cityName" class="mr-2 col-form-label text-right">縣市</label>
              <!--等寬-->
              <div class="flex-fill">
              <!--切換縣市時移除上次的地標-->
                <select id="cityName" class="form-control"
                v-model="select.city" @change="removeMarker()">
                  <option value="">-- Select One --</option>
                  <option :value="c.CityName" v-for="c in cityName" :key="c.CityName">
                  {{ c.CityName }}
                  </option>
                </select>
              </div>
            </div>

        <div class="form-group d-flex">
            <label for="area" class="mr-2 col-form-label text-right">地區</label>
            <div class="flex-fill">
              <select id="area" class="form-control" v-if="select.city.length"
                v-model="select.area" @change="removeMarker();updateMap()">
                <option value="">-- Select One --</option>
                <!--find 回傳第一個滿足函式的元素值-->
                <option :value="a.AreaName"
                  v-for="a in cityName.find((city) => city.CityName === select.city).AreaList"
                  :key="a.AreaName">
                  {{ a.AreaName }}
                </option>
              </select>
            </div>
        </div>

        <div class="form-group d-flex">
            <label for="area" class="mr-2 col-form-label text-right">藥局</label>
            <div class="flex-fill">
              <div class="input-group">
                <input class="form-control" type="text" v-model="searchText"
                  placeholder="Search" aria-label="Search" @input="search()">
              </div>
            </div>
        </div>
<!--
        <template v-for="(item, key) in filterData">
            <a class="list-group-item text-left" :key="key"
              v-if="item.properties.name === this.searchText"
              :class="{ 'highlight': item.properties.mask_adult || item.properties.mask_child}"
              @click="panTo(item)">
              <h3>{{ item.properties.name }}</h3>
              <p class="mb-0">
                成人口罩：{{ item.properties.mask_adult}} | 兒童口罩：{{ item.properties.mask_child}}
              </p>
              <p class="mb-0">地址：<a :href="`https://www.google.com.tw/maps/place/${item.properties.address}`"
                target="_blank" title="Google Map">
                {{ item.properties.address }}</a>
              </p>
            </a>
        </template> -->

        <p class="mb-0 small text-muted text-right">請選擇區域查看</p>
      </div>
      <ul class="list-group">
        <!-- <template>
          <a class="list-group-item text-left">
            <h3>藥局名稱</h3>
            <p class="mb-0">
              成人口罩：1 | 兒童口罩：2
            </p>
              <p class="mb-0">地址：<a href="https://www.google.com.tw/maps/place/..."
              target="_blank" title="Google Map">
              地址</a>
            </p>
          </a>
        </template> -->
        <template v-for="(item, key) in filterData">
            <a class="list-group-item text-left" :key="key"
              v-if=" searchText != ''"
              :class="{ 'highlight': item.properties.mask_adult || item.properties.mask_child}"
              @click="panTo(item)">
              <h3>{{ item.properties.name }}</h3>
              <p class="mb-0">
                成人口罩：{{ item.properties.mask_adult}} | 兒童口罩：{{ item.properties.mask_child}}
              </p>
              <p class="mb-0">地址：<a :href="`https://www.google.com.tw/maps/place/${item.properties.address}`"
                target="_blank" title="Google Map">
                {{ item.properties.address }}</a>
              </p>
            </a>
          </template>
        <template v-for="(item, key) in data">
            <a class="list-group-item text-left" :key="key"
              v-if="item.properties.county === select.city
                && item.properties.town === select.area && searchText === ''"
              :class="{ 'highlight': item.properties.mask_adult || item.properties.mask_child}"
              @click="panTo(item)">
              <h3>{{ item.properties.name }}</h3>
              <p class="mb-0">
                成人口罩：{{ item.properties.mask_adult}} | 兒童口罩：{{ item.properties.mask_child}}
              </p>
              <p class="mb-0">地址：<a :href="`https://www.google.com.tw/maps/place/${item.properties.address}`"
                target="_blank" title="Google Map">
                {{ item.properties.address }}</a>
              </p>
            </a>
          </template>
      </ul>
    </div>
  </div>

  <!--地圖-->
    <div class="col-sm-9">
      <div id="map"></div>
    </div>
  </div>
</div>
